//forms


.doGreenText(@btn:false){
    position: relative;

    &.focus{
        & > input{
            padding-left: 12px !important;
        }

        & > i{
            display: none;
        }
    }

    & > input[type="text"]{
        display: block;
        width: 100%;
        background: #edfaf8;
        border: 1px #69aaac solid;
        font-size: 14px;
        color: #415b70;
        padding: 0 12px;

        line-height: 42px;
        display: block;
        width: 100%;



        & when(@btn = icon){
            padding-left: 42px;
            & + i{
                position: absolute;
                color: #8FA3AD;
                left: 12px;
                .center-y;
                .trans-all;
            }


        }



        & when(@btn = button){
            padding-right: 42px;
            & + button{
                display: inline-block;
                position: absolute;
                right: 12px;
                top: 7px;
                .trans-all;
                &:hover{
                    color: tint(@oil, 30%);
                }

                @media(max-width:600px){

                    font-size: 12px;
                    right: 8px;
                    top: 13px;

                }
            }
        }
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: @oil;
        }
        &::-moz-placeholder { /* Firefox 19+ */
            color: @oil;
        }
        &:-ms-input-placeholder { /* IE 10+ */
            color: @oil;
        }
        &:-moz-placeholder { /* Firefox 18- */
            color: @oil;
        }
        &:focus{
            outline: 0;

        }
    }

    @media(max-width:600px){
        //padding-right: 32px;
        & > input[type="text"]{
            font-size: 12px;
        }
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            font-size: 12px;
        }
        &::-moz-placeholder { /* Firefox 19+ */
            font-size: 12px;
        }
        &:-ms-input-placeholder { /* IE 10+ */
            font-size: 12px;
        }
        &:-moz-placeholder { /* Firefox 18- */
            font-size: 12px;
        }

    }

}

.doGreenSelect{
    position: relative;
    & > .select2-container{
        width: 100%;
        & > a{
            background: #edfaf8;
            border: 1px #69aaac solid;
            border-radius: 0;
            color: @oil;
            padding: 0 12px;
            padding-right: 42px;
            line-height: 42px;
            display: block;
            width: 100%;
            overflow: hidden;
            & > .select2-arrow{
                border-top-color: @oil;
            }
            & > .select2-chosen {
                white-space: nowrap;
            }
            @media(max-width:600px){
                font-size: 12px;
            }
        }


    }
}
