// Widget upload

.upload-widget{
    background: #fff;
    width: 100%;
    border: 2px #ccc dashed;
    padding: 32px 16px;
    text-align: center;

    & > .dz-message > img{
        display: block;
        width: 39px;
        margin: 0px auto;
        margin-bottom: 24px;
    }
    & > .dz-message > i{
        display: block;
        font-size: 32px;
        color: #78909c;
    }
    & > .dz-message > h3{
        font-size: 18px;
        color: #78909c;
        text-align: center;
        font-weight: 500;
        margin: 0;
    }
    & > .dz-message > small{
        display: block;
        font-size: 14px;
        color: #78909c;
        text-align: center;
        font-weight: 400;
    }

    @media(max-width:460px){
        & > .dz-message > h3{
            font-size: 14px;
        }
        & > .dz-message > small{
            font-size: 12px;
        }
    }
}
