// cabecera DrPlus

nav.paciente-nav{
    background-color: #1A3661;
    height: 60px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    a {
        text-decoration: none !important;
    }
}
.paciente-nav-container{
    .clearFix;
    position: relative;
}
.paciente-nav-usr{
    display: inline-block;
    float: left;

    & .paciente-nav-trigger{
        display: block;
        height: 60px;



        & i{
            font-size: 24px;
            margin-right: 4px;
            color: @white;
            position: relative;
            top: 14px;
            .trans-all;

            @media(max-width:600px){
                display: none;
            }
        }
        & img{
            border-radius: 50%;
            display: inline-block;
            width: 42px;
            position: relative;
            top:9px;
        }
        & label{
            display: inline;
            position: relative;
            top: 8px;
            margin-left: 4px;
            font-size: 12px;
            color: @white;
            .trans-all;

            @media(max-width:800px){
                display: none;
            }

        }
        &:hover{
            & i{
                color: #D7D7D7;
            }
            & label{
                color: #D7D7D7;
                cursor: pointer;
            }
        }
        & .nombre-corto {   
            position: absolute;
            top: 9px;
            color: #333333;
            left: 0;
            text-align: center;
            font-size: 20px;
            font-weight: 800;
            letter-spacing: 2px;
            width: 42px;
            height: 42px;
            line-height: 42px;
        }
    }

    & ul.paciente-nav-usr-menu{
        display: none;
        padding: 2px;
        background-color: #F7F7F7;
        .shadow-z-2;



        & > li{
            padding: 8px 16px;
            border-bottom: 1px #ccc solid;
            margin: 0 5px;
            font-size: 14px;

            & a{
                display: block;
                width: 100%;
                color: #989897;
                .trans-all;
                &:hover{
                    color:@oil;
                }

                i{
                    font-size: 18px;
                    margin-right: 12px;

                    &.icon-doctorplus-manual{
                        position: relative;
                        top: 2px;
                    }
                }
            }

            &:last-child{
                border-bottom:0;
            }

        }

        & i.icon-doctorplus-gear {
            position: relative;
            top: 2px;
        }
        & i.icon-doctorplus-dollar-circular {
            position: relative;
            top: 1px;
        }

        @media(max-width:600px){
            font-size: 12px;
            position: relative;
            left: -17px;
        }
    }
}
.paciente-nav-logo{
    display: inline-block;
    position: absolute;
    .center-x;
    top: 12px;
    & img{
        display: block;
        width: 120px;
        height: auto;
    }
    @media(max-width:600px){
        top: 16px;	
    }
}
.hin-doctorplus-rsp-logo-lg,.hin-doctorplus-rsp-logo{
    & img {
        width: 120px;
    }
}
.paciente-nav-menu{
    display: inline-block;
    float: right;

    & .paciente-nav-menu-burger{
        display: none;
        &:hover{
            cursor: pointer;
        }

        @media(max-width:600px){
            display: inline-block;
            color: #fff;
            font-size: 24px;
            float: right;
            position: relative;
            top: 10px;
        }
    }

    & ul.paciente-nav-menu-rsp{
        position: relative;
        top: 17px;

        & li{
            display: inline-block;
            margin-right: 8px;
            &:last-child{
                margin-right: 0;
            }

            & a{
                color: @white;
                .trans-all;
                & i{
                    font-size: 24px;

                    & .pass{
                        width: 24px;
                    }

                }
                & label{
                    display: none;
                }
                &:hover{
                    color: #D7D7D7;
                }
                &.ocultar-miembros{
                    color: #46525e;
                    .trans-all;

                    &:hover{
                        color: tint(#46525e,50%);
                    }
                }
            }

        }
        // menu sec
        @media(max-width:600px){
            display: none;
            .clearFix;
            position: relative;
            left: 15px;

            & li{
                display: block;
                background-color: #F33243;
                margin-right: 8px;
                width: 100%;

                &:last-child{
                    margin-right: 0;
                }

                & a{
                    color: @white;
                    display: block;
                    width: 100%;
                    padding: 4px 17px;

                    .trans-all;
                    & i{
                        font-size: 14px;
                        position: relative;
                        top: 1px;
                        margin-right: 1px;
                        & .pass{
                            width: 19px;
                        }
                    }
                    & label{
                        display: inline-block;
                        margin-left: 4px;
                        font-size: 12px;
                    }
                    &:hover{
                        color: #D7D7D7;
                        background-color: tint(#F33243,30%);
                    }
                }

            }
        }

    }
}


