// float nav

@fnIndex: 9001;
@fn-size: 64px;
@fn-size-sm: 46px;
@fnbg:#007D8B;
@fnbg-hover:#00b0ad;




.fn-bubble(@size:@fn-size){
    display: block;
    width: @size;
    height: @size;
    line-height: @size;
    text-align: center;
    border-radius: @size;
    color: @white;

    @media(max-width:600px){
        @size:@fn-size-sm;
        width: @size;
        height: @size;
        line-height: @size;
        border-radius: @size;
    }
}

.float-nav{
    position: fixed;
    bottom: 24px;
    right: 24px;
    border-radius: @fn-size;
    z-index: 1000000;

    @media(max-width:600px){
        bottom: 6px;
    }
}
.fn-box{
    width: @fn-size;
    min-height: @fn-size;
    border-radius: @fn-size;
    position: relative;

    @media(max-width:600px){
        width: @fn-size-sm;
        min-height: @fn-size-sm;
        border-radius: @fn-size-sm;
    }

}

.fn-usr-trigger{
    color: @white;
    .fn-bubble;
    font-size: 28px;
    border-radius: 50%;
    background-color: #108c7d;
    position: absolute;
    top: 0;
    left: 0;
    .shadow-z-2;
    & i{
        position: relative;
        top: 3px;
        left: 0px;
        display: block;
        width: 24px;
        overflow: hidden;
        margin: auto;

        @media(max-width:600px){
            width: 18px;
        }
    }

    .trans-all;
    z-index: @fnIndex + 2;
    &:hover{
        color: @white;
        background-color: tint(#108c7d,30%);
    }


    @media(max-width:600px){
        font-size: 24px;
    }

    & .gotmsg{
        display: block;
        width: 16px;
        height: 16px;
        background-color: #ED3545;
        border: 2px @white solid;
        border-radius: 50%;
        position: absolute;
        top: 10px;
        right: 12px;
        z-index: 1000;

        @media(max-width:600px){
            width: 12px;
            height: 12px;
            top: 8px;
            right: 7px;
        }
    }

}


.fn-menu{
    background-color: @fnbg;
    width: @fn-size;
    border-radius: @fn-size;
    min-height: @fn-size;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: @fnIndex + 1;
    .shadow-z-2;

    display: none;

    @media(max-width:600px){
        width: @fn-size-sm;
        border-radius: @fn-size-sm;
        min-height: @fn-size-sm;
    }
}

.fn-close{
    .fn-bubble;
    margin: auto;
    font-size: 20px;
    &:hover{
        -webkit-transform: rotateZ(-90deg);
        -ms-transform: rotateZ(-90deg);
        transform: rotateZ(-90deg);
    }
}

.fn-action-list{
    display: block;
    width: @fn-size;

    @media(max-width:600px){
        width: @fn-size-sm;
    }



    & li{
        position: relative;
    }


    & .fn-list-action-item{
        position: relative;
        .fn-bubble;
        margin: auto;
        background-color: @fnbg;
        font-size: 24px;

        @media(max-width:600px){
            font-size: 20px;
        }

        &:hover{
            background-color: tint(@fnbg, 30%);

            & .fn-tooltip{
                display: inline-block;
            }

        }

        &.selected{
            background-color: tint(@fnbg, 30%);
        }
        &.selected-submenu{
            background-color: tint(@fnbg, 30%);
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            width: 100%;
        }


        @media(max-width:800px){
            &:hover{
                & .fn-tooltip{
                    display: none;
                }
            }

        }
    }

    & .gotmsg{
        display: block;
        width: 12px;
        height: 12px;
        background-color: #ED3545;
        border-radius: 50%;
        position: absolute;
        top: 14px;
        right: 14px;

        @media(max-width:600px){
            top: 7px;
            right: 4px;
        }
    }

    & .fn-tooltip{
        font-size: 14px;
        color: @white;
        display: inline-block;
        position: absolute;
        top: 12px;
        right: 72px;
        line-height: 32px;
        border-radius: 4px;
        padding: 0 7px;
        text-align: left;
        display: none;

        & .fn-arrow{
            position: absolute;
            right: -12px;
            top: 10px;
        }

        & .fn-item-content{
            display: inline-block;
            white-space: nowrap;
        }

    }
}

.fn-usr{
    & .fn-tooltip{
        background-color: #007d8b;
        & .fn-arrow{
            border-right:6px transparent solid;
            border-left:6px #007d8b solid;
            border-top:6px transparent solid;
            border-bottom:6px transparent solid;
        }
    }
}

.fn-mdc{

    & .fn-usr-trigger{
        background-color: #F33243;
        &:hover{
            background-color: tint(#F33243, 30%);
        }

        & .gotmsg{
            display: block;
            width: 16px;
            height: 16px;
            background-color: #41cac0;
            border: 2px @white solid;
            border-radius: 50%;
            position: absolute;
            top: 10px;
            right: 12px;
            z-index: 1000;

            @media(max-width:600px){
                width: 12px;
                height: 12px;
                top: 8px;
                right: 7px;
            }
        }
    }
    & .fn-tooltip{
        background-color: #F33243;
        & .fn-arrow{
            border-right:6px transparent solid;
            border-left:6px #F33243 solid;
            border-top:6px transparent solid;
            border-bottom:6px transparent solid;
        }
    }
}


.fn-mdc-usr-holder{
    background-color: tint(@fnbg, 30%);
    position: absolute;
    top: 0;
    right: @fn-size;
    white-space: nowrap;
    padding: 11px 8px;
    display: none;
    border-bottom-left-radius: 35px;
    border-top-left-radius: 35px;
    & ul{
        list-style: none;
        font-size: 0;
        & li{
            display: inline-block;
            margin-left: 8px;
            position: relative;
            width: 42px;
            height: 42px;

            &:first-child{
                margin-left: 0;
            }

            & a{
                &  img{
                    display: block;
                    width: 42px;
                    border-radius: 50%;
                    border: 0;

                }
                &:hover{
                    &  img{
                        border: 2px tint(@fnbg, 30%) solid;
                    }
                }
            }

            .menu-float-usr-icon{
                display: block;
                width: 42px;
                height: 42px;
                line-height: 42px;
                background-color: #1dd4dd;
                text-align: center;
                color: #036b70;
                font-size: 18px;
                border-radius: 50%;
                margin: 0;
                padding: 0;
                position: absolute;
                top: 1px;
            }

            @media(max-width:800px){

                margin-left: 8px;
                margin-bottom: 8px;

                &:first-child{
                    margin-left: 8px;
                }
            }

        }
    }
    @media(max-width:800px){
        width: 126px;
        border-bottom-left-radius: 14px;
        border-top-left-radius: 14px;
        padding-bottom: 0;
        white-space: normal;
        padding: 6px 8px;
    }
    @media(max-width:600px){
        right: @fn-size-sm;
    }
}

