@import "_bootstrap-stars.less";

// nueva consulta nav
.container-in{
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
}
.ce-nc{
    &-main-nav{
        min-height: 150px;
        background: #eceff1;
        width: 100%;
        padding-top: 48px;

    }
    //--- holder left
    &-back-holder{

    }
    &-back{
        & > a{
            font-size: 24px;
            white-space: nowrap;
            .trans-all;
            & > figure{
                background: #d8d8d8;
                display: inline-block;
                padding: 4px 4px;
                margin-right: 12px;
                & > i{
                    color: @white;
                    font-size: 24px;
                    .trans-all;
                }
                .trans-all;
            }
            &:hover{
                color: #69aaac;
                & > figure{
                    background: tint(#d8d8d8,30%);
                    & > i{
                        color: #69aaac;
                    }
                }
            }

            @media(max-width:@screen-xs){
                & > figure{
                    padding: 0px 4px;
                    margin-right: 0;
                    & > i{
                        position: relative;
                        top: 2px;
                    }
                }
            }

            @media(max-width:@screen-md){
                font-size: 18px;
            }
            @media(max-width:650px){
                & > span{
                    display:none;
                }
            }
        }


    }

    //--- holder right
    &-step-holder{
        position: relative;

        ul.ce-nc-steps{
            display: block;
            width: 100%;
            margin: 0;
            padding: 0;
            clear: both;
            &:after{
                content: "";
                display: table;
                clear: both;
            }

            & > li.ce-nc-step{
                float: left;
                width: 25%;
                position: relative;


                &  figure{
                    .doCircle(56,px);
                    background: #f2f2f2;
                    border: 1px #69aaac solid;
                    font-size: 18px;
                    line-height: 56px;
                    text-align: center;
                    color: #69aaac;
                    font-weight: bold;
                    float: right;
                }
                &.active{

                    & figure{
                        background: #69aaac;
                        color: #f2f2f2;
                        .trans-all;
                    }
                    & > a{
                        &:hover{
                            & > figure{
                                background: tint(#69aaac,30%);
                            }
                        }
                    }
                }
                &:first-child{
                    &  .ce-nc-step-divider{
                        display: none;
                    }
                }
                &:last-child{
                    &  figure{
                        //float: right;
                    }

                }

                @media(max-width:@screen-xs){
                    &  figure{
                        .doCircle(42,px);
                        line-height: 42px;
                    }
                }

            }
        }

    }
    &-step-divider{
        height: 2px;
        background: #69aaac;
        width: 100%;
        position: absolute;
        .center-y;
    }
}
#div_seleccion_tipo{

    padding-top: 42px;
    padding-bottom: 0px;
    @media(max-width:@screen-xs){
        padding-top: 30px;
    }
    & .cs-nc-p2-header{
        figure {
            & label.checkbox{
                position: absolute;
                left: -40px;
                margin-top: 5px;
            }
        }
    }

    & .check-tipo-consulta{
        cursor:pointer;
        &:hover{
            h2{
                font-weight: 600;
            }
        }
    }


}

//---- section 
.ce-nc-consulta{
    padding-top: 42px;
    padding-bottom: 42px;
    &-title-divider{
        width: 100%;
        border-top:1px #ccc solid;
        position: relative;
        height: 10px;
        & > span{
            display: block;
            width: 20px;
            height: 10px;
            position: absolute;
            .center-x;
            top: -1px;
            background:@white url(../imgs/consulta-express/arrow-bottom.png) top center no-repeat;
        }
    }
    & .ce-nc-p4-arrow-divider{
        @media(max-width:@screen-sm){
            margin-bottom: 20px;
        }
    }
    &-paciente{

        & > h3{
            font-size: 18px;
            font-weight: 300;
            text-align: center;
            line-height: 42px;
            padding: 0;
            margin: 0;

            @media(max-width:@screen-xs){
                font-size: 14px;
                line-height: 16px;
                padding-bottom: 14px;
            }
        }

        &-holder{
            clear: both;
            margin-top:32px;
            &:after{
                content: "";
                display: table;
                clear: both;
            }
        }
        &-icon{
            float: left;
            width: 15%;
            position: relative;
            top: 14px;

            & > i{
                font-size: 32px;
            }
        }
        &-content{
            float: right;
            position: relative;
            width: 84%;
            background: #c9f2ee;
            border-radius: 5px;
            min-height: 80px;

            & > p{
                display: block;
                font-size: 18px;
                line-height: 22px;
                position: absolute;
                text-align: center;

                width: 90%;
                .center-xy;

                @media(max-width:@screen-xs){
                    font-size: 14px;
                    line-height: 16px;
                    padding-bottom: 14px;
                }

            }

            & > span{
                display: block;
                position: absolute;
                background: url(../imgs/consulta-express/arrow-left-lb.png) right center no-repeat;
                width: 8px;
                height: 14px;
                left: -8px;
                .center-y;
            }

        }
    }
    &-medico{
        & > h3{
            font-size: 18px;
            font-weight: bold;
            text-align: center;
            line-height: 42px;
            padding: 0;
            margin: 0;
            color: #ed3545;

            @media(max-width:@screen-xs){
                font-size: 14px;
                line-height: 16px;
                padding-bottom: 14px;
            }

        }
        &-lnk{
            display: block;
            width: 100%;

            & > figure{
                .doCircle(90,px);
                background: #f2f2f2;
                margin: 0 auto;
                margin-top: 32px;
                position: relative;
                text-align: center;

                & > i{
                    font-size: 48px;
                    line-height: 90px;
                    &.icon-doctorplus-user-add-like{
                        font-size: 32px;
                        position: relative;
                        top: -2px;
                        left: 3px;
                    }
                }
            }
            & > span{
                text-align: center;
                font-size: 14px;
                display: block;
                font-weight: 400;
                line-height: 18px;
                margin-top: 14px;

                @media(max-width:@screen-xs){
                    font-size: 12px;
                    line-height: 14px;;
                }
            }
            &:visited, &:focus, &:link{
                color: @oil;
            }

            &:hover{
                color: @oil;

                & > figure{
                    background: #d8d8d8;
                }

            }
            &:active{
                & > figure{
                    background: #ff6f6f;
                    color: @white;
                }
            }
            &.active{
                & > figure{
                    background: #ff6f6f;
                    color: @white;
                }
            }
        }

    }

    &-bottom{
        padding-top: 42px;

        &-lnk-holder{
            padding: 24px 0;
            position: relative;
            text-align: center;
            & > a:first-child{
                background-color: transparent !important;
                border: none;
                color: @oil !important;
                &:hover{
                    color: #21b797 !important;
                    border: none !important;
                    background-color: transparent !important;
                }
            }

            & > a{
                .doGreenButton;
                & >span{
                    .doArrow(14,px,#fff);
                    position: relative;
                    top: 1px;
                    margin-left: 9px;
                }

                .trans-all;
                &:hover{
                    background: tint(#0ec2a1,30%);
                }
            }
        }
        &-lnk-politicas{
            .doWhiteButton;
            border-color: #ccc;
            &-holder{
                text-align: center;
            }

            font-size: 14px;
            text-align: center;

            @media(max-width:@screen-xs){
                font-size: 12px;
                line-height: 14px;
                padding-bottom: 14px;
            }
        }
    }

}

/// slide
.ce-nc-consulta-bottom-slide{
    min-height: 462px !important;
    padding-top: 57px !important;
}

// tabla precios
.cs-nc-precios-holder{
    background: #edfaf8;
    width: 100%;
    border-top: 1px #ccc solid;
}
.ce-nc-consulta-precios{
    background: #edfaf8;
    padding-bottom: 42px;
    padding-top: 24px;

    &  h2{
        font-size: 24px;
        font-weight: 400;
        color: @oil;
        text-align: center;
        margin-bottom: 42px;
    }

    &-holder{
        text-align: center;
        & > a{
            display: block;
            margin: 0 auto;
            &:hover{
                & >figure > i{
                    color: #48c9b0;
                }
                & > h4{
                    color: #48c9b0 !important;
                }
                & > span.fecha-carga{
                    color: @oil;
                }
            }

        }
        & > figure, & a > figure{
            width: 62px;
            height: 62px;
            margin: 0 auto;

            & > i{
                font-size: 62px;
                color: #f33243;
                line-height: 62px;
                position: relative;
                top: -4px;
                .trans-all;
            }

            @media(max-width:600px){

                & > i{
                    font-size: 54px;
                }
            }
        }

        & > figure.saldo{
            .doCircle(62,px);
            background: #ff6f6f;
            line-height: 62px;
            text-align: center;
            margin: 0 auto;

            & > i{
                font-size:38px;
                color: #edfaf8;
                position: relative;
                top: 4px;
            }

            @media(max-width:600px){
                .doCircle(54,px);
                & > i{
                    font-size:32px;
                    top: 0px;
                }
            }
        }



        &  h4{
            font-size: 16px;
            font-weight: 400;
            color: #f33243;
            padding-bottom: 0;
            margin-bottom: 0;
        }
        &.paciente-empresa{
            @media(min-width:600px){
                h4{
                    height: 34px;
                }
            }
        }

        & > a{
            &:hover{
                & > h4{
                    color: tint(#f33243,30%);
                }

            }
        }

        & > span{
            font-size: 32px;
            color: @oil;

            & > small{
                font-size: 18px;
            }

            @media(max-width:@screen-xs){
                //font-size: 18px;

                & > small{
                    //font-size: 12px;
                }
            }

        }
        & a > span.fecha-carga{
            font-size: 14px;
            font-weight: 400;
            display: inline-block;
            padding-top: 24px;

            @media(max-width:@screen-xs){
                padding-top: 4px;
            }

        }
    }
}

.ce-nc-consulta-precios-tabla{
    &-titulo{
        padding-top: 24px;
        & > h4{
            font-size: 18px;
            color: #f33243;

            & > a{
                color: @oil;
                font-size: 14px;
                margin-left: 12px;
                font-weight: 400;

                & > span{
                    .doArrow(14,px,@oil);
                    top:3px;
                    margin-left: 4px;
                }

                .trans-all;

                &:hover{
                    color: tint(@oil,30%);
                }


                @media(max-width:@screen-xs){
                    display: block;
                    margin-left: 0;
                    padding-top: 18px;
                    padding-bottom: 18px;
                }
            }
        }

    }
    &-holder{
        // display: none;
        @media(max-width:@screen-sm){
            overflow-x: scroll;
        }
    }
    &-footer{
        font-size: 12px;
        color: @oil;
        font-weight: 400;
        line-height: 14px;
        margin: 0;
        padding: 0;
        display: block;
    }
}
// tabla
.nc-tabla-precios{
    padding-bottom: 42px;
    width: 100%;
    display: table;

    & > .nc-header, & > .nc-row{
        clear: both;
        display: flex;
        &:after{
            content: "";
            display: table;
            clear: both;
        }
    }

    & > .nc-header{
        //border-bottom: 1px #46525e solid;
        //border-top: 1px #46525e solid;
        padding-top: 10px;
        padding-bottom: 10px;

        & > .nc-td{
            font-weight: bold;

            &:first-child{
                text-indent: 16px;
            }
        }
    }

    & > .nc-row{
        padding-top: 4px;
        padding-bottom: 4px;

    }

    &  .nc-td{
        float: left;

        padding-left: 4px;
        font-size: 14px;
        height: 32px;
        line-height: 32px;
        display: table-cell;
        overflow: hidden;
        border-bottom: 1px #ccc solid;

        &:nth-child(1){
            min-width: 15%;

            & > i{
                position: relative;
                top: 2px;
                left: -24px;
                display: none;
            }
        }
        &:nth-child(2){
            min-width: 30%;
            min-height: 1px;
        }
        &:nth-child(3){
            min-width: 30%;
            min-height: 1px;
        }
        &:nth-child(4){
            min-width: 25%;
            min-height: 1px;

            &.saldo-menor{
                color:#0ec2a1;
            }
        }
    }

    @media(max-width:@screen-sm){

        &  .nc-td{
            font-size: 12px;
            &:nth-child(1){
                min-width: 30%;
            }

            &:nth-child(2){
                min-width: 70%;

            }
            &:nth-child(3){
                min-width: 50%;

            }
            &:nth-child(4){
                min-width: 50%;

            }
        }
    }





}

.nc-row-small{
    min-height: 1px;
    padding: 4px 0; 
    display: none;
    & > .nc-td{
        &:nth-child(1){
            min-width: 30%;
            min-height: 1px;
            display: block;
            text-indent: 14px;
        }
        &:nth-child(2){
            min-width: 70%;
            min-height: 1px;
            display: block;
        }
    }
}

.nc-show{
    display: block;
}
.nc-symbol-red{
    color: #f33243; 
}
.nc-td-border{
    border: none !important;
}
.nc-rotate{
    transform: rotate(90deg);
}

// footer table
.ce-nc-footer-table{
    &-lnk{
        text-align: center;
        padding-top: 42px;
        padding-bottom: 42px;

        & > a{
            background: #ff6f6f;
            color: @white;
            text-align: center;
            display: inline-block;
            padding: 12px 24px;
            font-size: 16px;
            .trans-all;
            &:hover{
                background: tint(#ff6f6f, 30%);
            }
        }
    }
}

.ce-nc-pagination{
    display: none;
}
