//								PASO 2 
//---------------------------------------------------

@presencial:#ffaf51;
@video:#06a9ef;
@consulta:#30b99d;

.cs-nc-section-holder{
    width: 100%;
    background-color: #fff;
    padding-bottom: 24px;
}

//back
.cs-nc-p2{
    padding: 16px 16px;
}
//header
.cs-nc-p2-header{
    padding-bottom: 18px;
    & > figure{
        display: block;
        .doCircle(77,px);
        background: #ff6f6f;
        line-height: 77px;
        text-align: center;
        margin: 0 auto;

        & > i{
            color: @white;
            font-size: 42px;
            position: relative;
            top: 3px;
        }
        @media(max-width:@screen-xxs){
            .doCircle(64,px);
            line-height: 64px;

            & > i{
                font-size: 32px;
            }
        }
    }

    & > h2{
        font-size: 24px;
        color: @oil;
        text-align: center;
        font-weight: 300;

        @media(max-width:@screen-xxs){
            font-size: 18px;
        }
    }
}
// inner
.cs-nc-p2-inner{
    width: 100%;
    padding: 24px;

    background: @white;

    @media(max-width:@screen-xs){
        padding: 0;
        padding-top: 8px;
        padding-bottom: 8px;
    }
}

// toolbar
.cs-nc-p2-toolbar{
    clear: both;
    min-height: 42px;
    background: #f7f7f7;
    padding-left: 24px;
    padding-right: 24px;

    &:after{
        content: "";
        display: table;
        clear: both;
    }
    // left
    &-left{
        display: inline-block;
        float: left;
        height: 42px;

        // trashbin

        & > a{
            color: @oil;
            position: relative;
            top: 6px;
            & > span.trash-bin{
                font-size: 14px;
                @media(max-width:600px){
                    display: none;
                }
            }
            & > span.trash-icon{
                background: #f7f7f7;
                border: 1px #747579 solid;
                padding: 2px 4px;
                margin-right: 4px;
                font-size: 16px;
                .trans-all;


            }
            &:hover{
                & > span.trash-icon{
                    background-color: #f0eadf;
                    border: 1px #8b9fa8 solid;
                }
            }
        }

    }
    // right
    &-right{
        display: inline-block;
        float: right;
        height: 42px;

        & ul{
            & > li{
                display: inline-block;
                font-size: 12px;

                & > span.tarifas{
                    position: relative;
                    top:-2px;

                    @media(max-width:600px){
                        display: none;
                    }
                }

                &:first-child{
                    margin-right: 14px;
                }

                & > label.checkbox{
                    margin-right: 16px;

                    & > span.label{
                        position: relative;
                        top:-1px;

                        @media(max-width:1000px){
                            display: none;
                        }
                    }
                    & > span.icons{
                        position: absolute;
                        top: 1px;
                    }
                    & > i{
                        position: relative;
                        left: -2px;
                        top: 1px;
                        font-size: 18px;

                        &.presencial{
                            color: @presencial;
                        }
                        &.video{
                            color: @video;
                        }
                        &.consulta{
                            color: @consulta;
                        }
                    }

                    @media(max-width:@screen-xs){
                        margin-right: 0;

                        & > i{
                            left: -6px;
                        }
                    }

                }

            }

        }


    }
}
//@toolbar

// listado



.cs-nc-p2-listado, .cs-nc-p2-listado-segunda-opcion{
    padding: 42px 0;
    padding-bottom: 0;
    & > .listado-row{
        clear: both;
        border-bottom: 1px #ccc solid;


        &:after{
            content: "";
            display: table;
            clear: both;
        }

        & > div{
            padding-top: 24px;
        }

        & > .listado-item-usr{
            width: 30%;
            float: left;
            height: 120px;

            @media(max-width:720px){
                width: 100%;
                float: none;
                border-bottom: 1px #ccc solid;
            }

        }
        & > .listado-item-detail{
            width: 50%;
            float: left;
            height: 120px;

            @media(max-width:720px){
                width: 100%;
                float: none;
                border-bottom: 1px #ccc solid;
            }
        }
        & > .listado-item-plan{
            width: 20%;
            float: left;
            height: 120px;
            padding-top: 0;

            @media(max-width:720px){
                width: 100%;
                float: none;
            }
        }


    }

    //user avatar

    & .listado-item-usr-avatar-holder{
        width:64px;
        height: 62px;
        float: left;
        position: relative;
        left: 6px;

        & > figure{
            .doCircle(62,px);
            position: relative;
            overflow: hidden;

            & > img{
                position: absolute;
                width: 100%;
                .center-xy;
            }
        }

        & > a > figure{
            .doCircle(62,px);
            position: relative;
            overflow: hidden;

            & > img{
                position: absolute;
                width: 100%;
                .center-xy;
            }
        }

        & > label.checkbox{
            position: absolute;
            bottom: 0;
            left: 0;
        }

    }
    & .listado-item-usr-name-holder{
        width:100%;
        padding-left: 80px;

        & > div.nombre{
            font-size: 16px;
            line-height: 16px;
            color: @oil;
            padding-bottom: 6px;

            @media(max-width:@screen-md){
                font-size: 12px;
            }

        }
        & > div.especialidad{
            color: #1abca1;
            font-size: 14px;
            line-height: 16px;

            @media(max-width:@screen-md){
                font-size: 12px;
            }

        }
    }
}


.cs-nc-p2-listado-segunda-opcion{
    padding-top: 0;

    & .listado-item-detail{
        width: 55% !important;
    }
    & .listado-item-plan{
        width: 15% !important;
        & .value{
            font-size: 18px !important;
        }

        & .consulta-col{
            width: 100% !important;
        }
    }
    @media(max-width:720px){
        & .listado-item-detail{
            width: 100% !important;
        }
        & .listado-item-plan{
            width: 100% !important;
        }
    }
}



//-- listado ratting

.listado-item-detail{
    position: relative;
    padding: 6px;
    & > p{
        color: @oil;
        font-size: 14px;
        line-height: 16px;
        position: relative;
        top:-1px;

        @media(max-width:@screen-md){
            font-size: 12px;
        }
    }
    & > .listado-item-detail-ratting{
        position: absolute;
        bottom: -10px;

        & > ul{
            list-style: none;
            padding: 0;
            margin: 0;
            display: inline-block;
            & > li{
                display: inline-block;
                color: #ccc;

            }
            &.estrellas-1{
                & > li{
                    .ratting(1);
                }

            }
            &.estrellas-2{
                & > li{
                    .ratting(2);
                }
            }
            &.estrellas-3{
                & > li{
                    .ratting(3);
                }
            }
            &.estrellas-4{
                & > li{
                    .ratting(4);
                }
            }
            &.estrellas-5{
                & > li{
                    .ratting(5);
                }
            }
        }

        & > p{
            display: inline-block;
            padding: 0;
            font-size: 14px;
            margin-left: 4px;

            @media(max-width:@screen-md){
                font-size: 12px;
            }
            @media(max-width:@screen-xxs){
                & > span{
                    display: none;
                }
            }

        }
    }
}


ul.ratting{
    list-style: none;
