///plan

.listado-item-plan{
    & > .listado-item-plan-row{



        clear: both;
        &:after{
            content: "";
            display: table;
            clear: both;
        }

        & > div{
            width: percentage(4/12);
            float: left;
        }

        & .value{
            background: #f7f7f7;
            height: 70px;
            text-align: center;
            line-height: 70px;
            font-size: 14px;
            position: relative;
            color: @oil;

            & > small{
                display: block;
                width: 100%;
                line-height: 14px;
                position: absolute;
                .center-xy;
            }

            & > i{
                color: #cfd8dc;
            }
        }
        & .icon{
            background: #2a3b49;
            height: 50px;
            text-align: center;
            line-height: 50px;

            & > a{
                display: block;
                text-align: center;
                line-height: 50px;
                height: 50px;
                width: 100%;

                & > i{
                    font-size: 18px;
                    color:#eee;
                }
            }

        }

        & .preseencial-col{

            &.selected{
                & > .icon  i{
                    color: #ffaf51;
                }
            }
            & > .icon > a{
                &:hover{
                    color: #ffaf51;
                }
            }
        }

        & .video-col{
            & > .value{
                border-left: 2px @white solid;
                border-right: 2px @white solid;
            }
            &.selected{
                & > .icon  i{
                    color: #06a9ef;
                }
            }
            & > .icon > a{
                &:hover{
                    color: #06a9ef;
                }
            }
        }

        & .consulta-col{

            &.selected{
                & > .icon  i{
                    color: #30b99d;
                }
            }
            & > .icon > a{
                &:hover{
                    color: #30b99d;
                }
            }
        }



    }
}

