//------------------------------------------------------
//						Proefsionales en la red
//------------------------------------------------------


.cs-nc-p2{
    &-row-divider{
        border-bottom: 1px #ccc solid;
        padding-bottom: 0px;
        padding-top: 24px;

        & > div > label{
            line-height: 20px;

            @media(min-width:@screen-sm){
                position: relative;
                top: 6px;
            }
        }
    }
    &-input-holder{
        width: 100%;
        position: relative;
        padding-bottom: 4px;

        &.required{
            &:after{
                content: "*";
                display: block;
                position: absolute;
                right: -12px;
                top: -7px;
            }
        }

        & > input[type="text"]{
            background: #edfaf8;
            border: 1px #69aaac solid;
            font-size: 15px;
            color: @oil;
            padding: 0 12px;
            padding-right: 42px;
            line-height: 42px;
            display: block;
            width: 100%;
            margin-bottom: 20px;
            &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                color: @oil;
            }
            &::-moz-placeholder { /* Firefox 19+ */
                color: @oil;
            }
            &:-ms-input-placeholder { /* IE 10+ */
                color: @oil;
            }
            &:-moz-placeholder { /* Firefox 18- */
                color: @oil;
            }
            &:focus{
                outline: 0;

            }
            & + button{
                display: inline-block;
                position: absolute;
                right: 12px;
                top: 7px;
                .trans-all;
                &:hover{
                    color: tint(@oil, 30%);
                }
            }

            @media(max-width:600px){
                padding-right: 32px;
                &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                    font-size: 12px;
                }
                &::-moz-placeholder { /* Firefox 19+ */
                    font-size: 12px;
                }
                &:-ms-input-placeholder { /* IE 10+ */
                    font-size: 12px;
                }
                &:-moz-placeholder { /* Firefox 18- */
                    font-size: 12px;
                }
                & + button{
                    font-size: 12px;
                    right: 8px;
                    top: 13px;
                }
            }
        }

        //-- select
        & > .select2-container{
            width: 100%;
            cursor: pointer;
            & > a{
                background: #edfaf8;
                border: 1px #69aaac solid;
                border-radius: 0;
                color: @oil;
                padding: 0 12px;
                padding-right: 42px;
                line-height: 42px;
                display: block;
                width: 100%;
                & > .select2-arrow{
                    border-top-color: @oil;
                }
                @media(max-width:600px){
                    font-size: 12px;
                }
            }


        }
        & .dropdown-programas-container{
            .select2-choice{
                overflow: hidden;
                text-overflow: ellipsis;
            }
            & .item-seleccionado{
                white-space:nowrap;
            }
            & .dropdown-programas{
                width: 100%;
                border: solid 1px #ccc;
                position: absolute;
                margin-top: 0px;
                & > li > a{
                    font-weight: 600;
                    .tag-container{
                        position:relative;
                        .programa-destacado-tag{
                            display:none;
                            transform:scale(0.6);
                            top: 0px;
                            left: 10px;
                        }
                    }
                    &.select-programa.disabled:hover{
                        cursor:not-allowed;
                        background-color: #f5f5f5;
                    }
                }
                & .dropdown-submenu{
                    display: none;
                    list-style:circle;
                    background: #fff;

                    li{
                        padding: 4px 16px 4px 30px;
                        &:hover{
                            background:rgba(202, 206, 209, 0.5);
                        }
                        a{
                            line-height: 1.429;
                            color: #606d7a;
                        }
                    }

                }
                & .open{
                    &+.dropdown-submenu{
                        display:block !important;
                    }
                }
            }
        }

    }
}

//--- range

.cs-nc-p2-range-holder{
    position: relative;
}
.cs-nc-p2-bg-bars{
    & > ul{
        display: block;
        list-style: none;
        margin: 0;
        padding: 0;
        font-size: 0;

        & > li{
            margin: 0;
            padding: 0;
            display: inline-block;
            width: 2%;
            height: 0;
            background: rgba(118,118,118,0.5);
        }
    }
}
.cs-ui-range{
    height: 6px;
    background: @white;
    border: 1px #b7bfc8 solid;
    border-radius: 0;
    & > .ui-slider-range{
        background: #0ec2a1;
    }
    & > a.ui-slider-handle {
        background-color: #fff;
        border: 1px #ccc solid;
        border-radius: 50%;
        cursor: pointer;
        height: 24px;
        position: absolute;
        width: 24px;
        z-index: 2;
        transition: background .25s;
        margin-left: -9px;
        top: -10px;
    }

}

// rangos

.cs-ui-range-values{
    width: 100%;
    position: relative;
    height: 24px;
    padding-bottom: 42px;
    color: @oil;
    font-size: 14px;
}
.cs-ui-range-value{
    height: 24px;
    display: inline-block;
    position: absolute;
    top: 0;
    border: none;
    max-width: 25%;
    &.min{
        left: 0;
        text-align: left;
    }
    &.max{
        right: 0;
        text-align: right;
    }
}
.cs-ui-range-values-promedio{
    display: inline-block;
    height: auto;
    position: absolute;
    top: 2px;
    border: 1px #ccc solid;
    border-radius: 4px;
    .center-x;
    z-index: 20;
    &-box{
        line-height: 1;
        height: 100%;
        padding: 6px 12px;
        position: relative;
        text-align: center;

        & > span{
            position: relative;
            top: -1px;
        }

        & > .cs-ui-range-values-promedio-arrow{
            display: block;
            width: 10px;
            height: 8px;
            background: url(../imgs/consulta-express/arrow-top.png) left bottom no-repeat;
            position: absolute;
            top:-8px;
            .center-x;
        }


    }

}
// valoracion

.ce-nc-valoracion{

    clear: both;
    &:after{
        content: "";
        display: table;
        clear: both;
    }

    & > label{
        display: inline-block;
        float: left;
    }
    & > ul{
        display: inline-block;
        margin-left: 42px;
        float: left;
        & > li{
            & > label > i{
                position: relative;
                top: 0px;
                color: #f3d240;
                font-size: 16px;
            }
        }
    }
}

// action

.cs-nc-p2-row-action{
    border-bottom: none;
    .clearFix;
    padding-bottom: 24px;
    & > .cs-nc-p2-row-action-btns{
        display: inline-block;
        float: right;
        padding-top: 14px;
        padding-left: 15px;
        padding-right: 15px;
        & > .cs-nc-p2-cancel{
            font-size: 16px;
            font-weight: bold;
            color: @oil;
            background: @white;
            border: 1px #ccc solid;
            padding: 12px 24px;
            .trans-all;
            &:hover{
                color:#0ec2a1;
            }
        }
        & > .cs-nc-p2-submit{
            font-size: 16px;
            font-weight: bold;
            color: @white;
            background: #f99b9b;
            border: 1px #f99b9b solid;
            padding: 12px 24px;
            margin-left: 16px;
            .trans-all;
            &:hover{
                background: tint(#f99b9b,30%);
            }
        }

        @media(max-width:365px){
            display: block;
            width: 100%;
            float: none;
            & > .cs-nc-p2-cancel, & > .cs-nc-p2-submit{
                display: block;
                width: 100%;
                margin: 0;
                margin-bottom: 24px;
            }
        }
    }

}
