//-------------------------------------------
//					paso 2	Resultado
//-------------------------------------------
.ce-nc-p2-res{
    position: relative;
}
.nc-ce-p2res-search-bar{
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    @media(max-width:960px){
        margin-top: 20px;
    }
    & .btn-oil-square {
        @media(max-width:600px){
            width: auto;
            margin-bottom: 0px;
        }
    }
    & > .search-holder > button{
        .doButton(#ffffff, @oil, false);
        border: 1px #b7bfc8 solid;
        display: block;
        float: left;
        padding: 0px 24px;
        width: 24%;
        margin-bottom: 20px;
        position: relative;
        height: 36px;
        font-size: 14px;
        text-align: left;
        font-size: 12px;
        line-height: 14px;

        & > i{
            color: #d8d8d8;
            line-height: 12px;
            position: absolute;
            right: 16px;
            .center-y;
            .trans-all;
        }

        @media(max-width:745px){
            width: 48%;
            float: left;
            & > i{
                right: 8px;
            }
        }

        .trans-all;
        &:hover{
            color: #80bcbc;
            & > i{
                color: @oil;
            }
        }
        @media(max-width:370px){
            padding: 11px 18px;
            text-align: left;
            font-size: 11px;
        }
    }
    & > button{
        .doButton(#f99b9b, @white, false);
        border: 1px #f99b9b solid;
        display: block;
        float: left;
        margin-left: 1%;
        padding: 0px 24px;
        padding-bottom: 0px;
        width: 18%;
        margin-bottom: 20px;
        position: relative;
        height: 36px;
        font-size: 12px;
        line-height: 14px;

        & > i{
            line-height: 12px;
            position: absolute;
            right: 16px;
            .center-y;
        }

        @media(max-width:745px){
            &:nth-child(even){
                width: 48%;
                float: left;

            }
            &:nth-child(odd){
                width: 48%;
                float: left;
                margin-left: 0;
            }
            &:nth-child(3){

                position: relative;
                //top: 1px;
            }
        }


        @media(max-width:370px){
            padding: 11px 18px;
            text-align: left;
            font-size: 11px;
            & > i{
                right: 8px;
            }
        }

    }
}
.ce-nc-p2-res-list-holder{
    background: @white;
    padding: 10px 10px;
}
.ce-nc-p2-res-toolbar{
    background: #f7f7f7;
    clear: both;
    &:after{
        content: "";
        display: table;
        clear: both;
    }

    & > ul{
        display: block;
        float: left;
        width: 60%;
        //background-color: aquamarine;

        & > li{
            display: inline-block;
            &.selectall{
                margin-left: 6px;
                margin-right: 6px;

                & > a{
                    padding-left: 8px;
                    & > i{
                        font-size: 18px;
                        position: relative;
                        top: 2px;
                    }
                    & > span.label{
                        font-size:12px;

                        @media(max-width:440px){
                            & > span{
                                display: none;
                            }
                        }
                    }
                    &:hover{
                        color: inherit;
                        cursor: default;
                    }
                }
            }
            &.bloquear{
                & > a{
                    & > figure{
                        .doCircle(22,px);
                        display: inline-block;
                        background: #ff6f6f;
                        text-align: center;
                        line-height: 22px;
                        top: 2px;
                        margin-right: 4px;

                        & > i{
                            font-size: 12px;
                            color: @white;
                            position: relative;
                            top: -2px;
                        }
                    }
                    & > label{
                        font-size: 12px;
                        font-weight: 400;

                    }
                    &:hover{
                        color: inherit;
                        cursor: default;
                    }
                }


            }
        }

        @media (max-width: 380px){
            width: 100%;
        }
    }
    & .resultado-pulldown{
        float: right;
        width: 40%;
        padding-right: 6px;
        position: relative;
        top: -2px;

        & .dropdown-express{
            float: right;
            top: 0;
            width: 100%;
            & > .select2-container{
                margin-bottom: 0px;
                border-radius: 0;
            }
            & > .select{
                min-width:inherit;
                width: 100%;
            }
            & > .select-primary .select2-choice{
                background: #80bcbc;
                padding: 7px 39px 7px 15px;
                border-radius: 0;
                top: 6px;
                &:hover{
                    background: tint(#80bcbc,30%);
                }
            }


            & > button{
                display: block;
                height: 34px;
                margin-top: 5px;
                min-width: 150px;

                & >i{
                    top: 0;
                    left: 12px;
                    &:after{
                        margin: 0;
                    }
                }

            }
            & > ul{
                min-width: 150px !important;
                max-width: 150px;

                & > li{
                    & > a{
                        padding: 12px 10px !important;
                    }
                }

            }
        }
        @media(max-width:1000px) and (min-width:770px){
            width: 100%;
            padding-bottom: 6px;
            padding-left: 6px;
            top: -4px;
            & > .dropdown-express{
                width: 100%;
            }
        }
        @media(max-width:600px){
            width: 100%;
            padding-bottom: 6px;
            padding-left: 6px;
            top: -4px;
            & > .dropdown-express{
                width: 100%;

                & > button{
                    min-width: inherit;
                    width: 100%;
                }
                & > ul{
                    min-width: inherit !important;
                    width: 100%;
                }
            }
        }

    }
}

