}
.select2-drop .select2-highlighted > .select2-result-label{
    background-color: #6d6d6d;
}
// lista
.ce-nc-resultados-list{

    height: 500px;
    overflow: hidden;
    @media(max-width:600px){
        height: auto !important;
    }

    &  .resultados-list-row{
        clear: both;
        &:after{
            content: "";
            display: table;
            clear: both;
        }
        width: 100%;
        padding-top: 24px;

        &:hover{
            & .resultados-list-left{
                background: #eceff1;
                cursor: pointer;
            }
        }
    }

    & .resultados-list-left{
        width: 80%;
        float: left;
        padding: 12px 12px;
        // selected

        &.resultados-list-left-selected{
            background: #eceff1;
        }


        //img content
        & > .resultados-list-left-img{
            display: inline-block;
            width: 78px;
            padding: 4px;
            float: left;
            position: relative;
            &  figure{
                .doCircle(74,px);
                position: relative;
                overflow: hidden;

                & > img{
                    display: block;
                    position: relative;
                    .center-xy;
                }
            }
            & > .resultado-check{
                position: absolute;
                bottom: 12px;
            }

            & span.bloquear{
                display: block;
                width: 22px;
                height: 22px;
                position: absolute;
                bottom: 6px;
                & > a{
                    & > figure{
                        .doCircle(22,px);
                        display: inline-block;
                        background: #ff6f6f;
                        text-align: center;
                        line-height: 22px;
                        top: 2px;
                        margin-right: 4px;

                        & > i{
                            font-size: 12px;
                            color: @white;
                            position: relative;
                            top: -2px;
                        }
                    }
                    & > label{
                        font-size: 12px;
                        font-weight: 400;

                    }

                }


            }
        }


        & > .resultados-list-left-content{
            padding-left: 90px;
            height: 112px;
            & > h3{
