.cs-ca-chat-holder{
    width: 90%;
    margin: 0 auto;
    max-height: none !important;
    @media(max-width:990px){
        width: 100%;

    }

    & .chat-row{
        padding: 10px;
    }

    & .chat-line-holder{
        position: relative;
        .clearFix;
        & > img{
            display: block;
            position: absolute;
            border-radius: 50%;

            @media(max-width:600px){
                width: 42px;
            }

        }

        & > .chat-content{
            padding-left: 80px;

            & > figure{
                background-color: #f7f7f7;
                border-radius: 4px;
                position: relative;
                padding: 16px;
                min-height: 120px;

                & > .chat-content-date{
                    font-size: 12px;
                    color: #90a4ae;
                    line-height: 14px;
                    padding-bottom: 16px;

                    & > span{
                        color: @oil;
                        margin-right: 12px;
                        font-style: italic;
                        font-weight: 400;
                        @media(max-width:600px){
                            display: block;
                            width: 100%;
                            line-height: 16px;
                        }
                    }
                }

                & > p{
                    font-size: 14px;
                    color: @oil;
                    line-height: 16px;

                }

                & > .chat-content-arrow{
                    .doArrow(14,px,#f7f7f7);
                    position: absolute;
                    top: 25px;
                    left: -7px;
                    transform: rotate(180deg);

                    @media(max-width:600px){
                        top: 16px;
                    }

                }
                //figure
                @media(max-width:460px){
                    & > p{
                        font-size: 12px;
                        line-height: 14px;
                    }
                }
            }
            & .chat-content-attach{
                background-color: #efefef;
                height: 62px;
                padding: 15px;
                position: relative;
                top: -4px;
                border-bottom-left-radius: 4px;
                border-bottom-right-radius: 4px;

                & i, & a{
                    font-size: 16px;
                }
                & span.attach-name{
                    font-size: 14px;
                    margin-left: 15px;
                    position: relative;
                    top: -6px;
                }

                @media(max-width:600px){
                    text-align: center;
                    & i{
                    }
                    & span{
                        display: none;
                    }
                }

            }
            & .chat-audio-holder{
                & > audio{
                    width: 100%;
                }
            }

            @media(max-width:600px){
                padding-left: 51px;
            }
            @media(max-width:460px){
                padding-left: 0;
            }
        }

    }

    & .chat-line-answer{

        & > .chat-content{
            padding-left: 0;
            & > figure{
                background-color: #dff4f1;
                & > span{
                    transform: rotate(0deg) !important;
                    border-left: 7px solid #dff4f1 !important;
                    left: inherit !important;
                    right: -7px !important;
                }
            }
        }
    }

    & .chat-date-divider{
        position: relative;
        height: 24px;
        & > .chat-date{
            background-color: @white;
            color: #90a4ae;
            display: inline-block;
            position: absolute;
            line-height: 24px;
            padding: 0 52px;
            .center-xy;

            @media(max-width:600px){
                padding: 0 24px;
            }
            @media(max-width:460px){
                font-size: 14px;
            }
        }
        & > .chat-line-divider{
            border-top: 1px #90a4ae solid;
        }
    }

}

